.pointRecord{background-color:#fff}.pointRecord>li{display:block;width:100%;padding:10px 15px;position:relative}.pointRecord>li:not(:last-child){border-bottom:1px solid #E8E8E8}.pointRecord .record__tit,.pointRecord .record__time{max-width:80%;max-height:20px;word-break:keep-all;overflow:hidden;text-overflow:ellipsis}.pointRecord .record__time{color:#999;font-size:13px}.pointRecord .record__point{position:absolute;right:15px;top:50%;transform:translate(0, -50%)}
